.div-menu-item {
	width: 100%;
	border-radius: 5px;
	background-color: #eee;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}

.div-menu-item > div {
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.div-menu-item > div:nth-of-type(1) {
	display: flex;
	align-items: center;
	justify-content: center;
}

.div-menu-item img {
	width: 100%;
}

.div-menu-item p,
.div-menu-item strong {
	text-transform: capitalize;
}

.div-menu-item button {
	padding: 2px 5px;
	border-radius: 5px;
	color: white;
	border: 0;
}

.div-menu-item p:nth-of-type(2) button {
	background-color: green;
}
