#div-menu {
	background-color: white;
	padding: 20px 10px;
	border-radius: 5px;
	border: 1px solid #eee;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

#div-menu h2 {
	text-align: center;
	margin-bottom: 10px;
	font-size: 1.5rem;
	line-height: 1.5rem;
}

#div-menu #div-menu-items-container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 10px;
	max-height: 470px;
	overflow-y: auto;
}
