#div-modal-items {
	width: 60%;
	height: 500px;
	padding: 40px 20px;
	position: absolute;
	background-color: white;
	border-radius: 5px;
	top: 50vh;
	left: 50%;
	transform: translate(-50%, -50%);
	display: grid;
	grid-template-columns: 1fr 1.4fr;
	grid-gap: 20px;
	z-index: 5;
	border: 1px solid #eee;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

#div-modal-form {
	display: flex;
	flex-direction: column;
	max-height: 400px;
}

#div-modal-form #p-update-item {
	background-color: blue;
	color: white;
	padding: 2px 5px;
	border-radius: 5px;
	margin-top: 5px;
}

#div-modal-form p small {
	color: red;
}

#div-modal-form h2 {
	text-align: center;
	font-size: 1.5rem;
	line-height: 1.5rem;
}

#div-modal-form input,
#div-modal-form select {
	font-size: 1rem;
}

#div-modal-form select {
	margin-bottom: 5px;
}

#div-modal-form label {
	margin-top: 5px;
}

#div-modal-form button {
	padding: 2px 5px;
	font-size: 1rem;
	color: white;
	border-radius: 5px;
	border: none;
	margin-right: 5px;
	margin-top: 20px;
}

#div-modal-form #btn-add {
	background-color: green;
}

#div-modal-form #btn-update {
	background-color: blue;
}

#div-modal-form #btn-remove {
	background-color: red;
}

#div-modal-form #btn-clear {
	background-color: orange;
}

input[type="number"] {
	-moz-appearance: textfield;
}
input[type="number"]::-webkit-inner-spin-button {
	-webkit-appearance: none;
}

#div-modal-items #btn-close {
	position: absolute;
	font-size: 2rem;
	color: black;
	background: none;
	border: none;
	top: 10px;
	right: 20px;
}

#div-modal-items #div-modal-search-item input {
	font-size: 1rem;
}

#div-modal-items #div-modal-search-item > div {
	max-height: 333px;
	overflow-y: auto;
}

#div-modal-items #div-modal-search-item #table-search {
	border-collapse: collapse;
	table-layout: fixed;
	width: 100%;
	margin-top: 10px;
}

#div-modal-items #div-modal-search-item #table-search td {
	width: 25%;
	text-align: center;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	border-bottom: 1px solid #eee;
}

#div-modal-items #div-modal-search-item #table-search tr:nth-of-type(even) {
	background-color: #eee;
}

#div-modal-items #div-modal-search-item #table-search tr:hover {
	opacity: 0.8;
}
