#div-cart {
	position: relative;
	background-color: white;
	padding: 20px 10px;
	border-radius: 5px;
	border: 1px solid #eee;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

#div-cart h2 {
	text-align: center;
	margin-bottom: 10px;
	font-size: 1.5rem;
	line-height: 1.5rem;
}
#div-cart #div-cart-items-container {
	width: 100%;
	display: flex;
	flex-direction: column;
	overflow-y: auto;
	height: 480px;
}

#div-cart-total {
	position: absolute;
	width: 95%;
	bottom: 10px;
	padding: 0 10px;
	display: flex;
	justify-content: space-between;
}

#div-cart-total button {
	border: none;
	padding: 2px 5px;
	font-size: 1rem;
	line-height: 1rem;
	background: none;
	text-decoration: underline;
}

#div-cart p {
	align-self: center;
}
