#div-header {
	width: 100%;
	padding: 10px 40px;
	background-color: white;
	display: flex;
	justify-content: space-between;
	border-radius: 5px;
	margin-bottom: 10px;
	border: 1px solid #eee;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
}

#div-header button {
	border: none;
	padding: 2px 5px;
	font-size: 1rem;
	line-height: 1rem;
	background: none;
	text-decoration: underline;
}
