.div-cart-item {
	width: 100%;
	border-radius: 5px;
	background-color: #eee;
	display: grid;
	grid-template-columns: 2fr 1fr 1.5fr 1fr;
	margin-bottom: 10px;
}

.div-cart-item > * {
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.div-cart-item p,
.div-cart-item strong {
	text-transform: capitalize;
}

.div-cart-item > div {
	display: flex;
	justify-content: center;
	align-items: center;
}

.div-cart-item .p-manage-qty {
	display: flex;
	justify-content: flex-end;
	min-width: 50px;
	max-width: 60px;
	text-align: right;
}

.div-cart-item .p-manage-qty span {
	padding: 0 5px;
}

.div-cart-item .btn-manage-qty {
	padding: 2px 5px;
	border-radius: 5px;
	background-color: white;
	color: black;
	border: 0;
}

.div-cart-item .btn-cancel {
	padding: 2px 5px;
	border-radius: 5px;
	background-color: red;
	color: white;
	border: 0;
}
