#div-navigation {
	margin-bottom: 10px;
}

#div-navigation button {
	padding: 2px 5px;
	margin-right: 5px;
	background-color: white;
	border-radius: 5px;
	border: 0;
	text-transform: capitalize;
	transition: background 0.3s;
	font-size: 1rem;
}

#div-navigation button.active {
	background-color: #eee;
}

#div-navigation button:hover {
	background-color: #eee;
}
